<template>
  <div>
    <div>
      <el-card class="box-card">
        <div><h3>选择条件</h3></div>
    <div class="div_bjground"  >
      <!-- 这是组内层 -->
      <div v-for="(select,index) in selectsCom" :key="index" class="item_div">
        <el-select v-model="select.field" filterable clearable placeholder="字段" :filter-method="filterValue" class="margin-right" size="small" @change="ctrlInput">
        <el-option
          v-for="item in cities"
          :key="item.value"
          :label="item.label"
          :value="item.value">
          <span style="float: left">{{ item.label }}</span>
          <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
        </el-option>
        </el-select>
        <el-select v-model="select.express" placeholder="运算符" @change="setCode($event,index)" size="small" class="margin-right">
          <el-option v-for="item in expreAll" :key="item.value" :label="item.label" :value="item.value">
            <span style="float: left">{{ item.label }}</span>
            <!--<span style="float: right; color: #8492a6; font-size: 16px">{{ item.value }}</span>-->
          </el-option>
        </el-select>
        <div style="display:inline-block;position:relative;" v-show="selectsCom[index].express!='empt'">
          <el-input v-model="select.value" size="small" clearable></el-input>
        </div>
        <el-select v-model="select.groupExpress" size="small" style="width:80px">
          <el-option value="&&" label="且">
            <span style="float: left">且</span>
          </el-option>
          <el-option value="||" label="或">
            <span style="float: left">或</span>
          </el-option>
        </el-select>
        <el-button size="small" icon="el-icon-plus" @click="addRowCom"></el-button>
        <el-button size="small" icon="el-icon-minus" @click="delRowCom(index)"></el-button>
      </div>
    </div>
    <div><el-button @click="generateJavaScript" type="primary">生成JS</el-button></div>
  <div style="height:100%" @dblclick="copy">
    <h3>脚本生成 <el-link @click="copy"><i class="el-icon-document-copy"></i>复制</el-link>&nbsp;<el-link :type="check_js" @click="checkJS"><i class="el-icon-view el-icon-check"></i>校验</el-link></h3>
    <highlightjs language="javascript" :code="tareaCode"></highlightjs>
  </div>
  </el-card>
    </div>
  </div>
</template>
<script >

import { fields } from '../plugins/fields.js'
export default {
  name: 'app',
  data () {
    return {
      check_js: 'warning',
      upload_file: '',
      index: 0,
      expression: '',
      tareaCode: '',
      selectsCom: [{ groupExpress: '&&', field: '', express: 0, value: '' }],
      expare: [String.raw`/(^$|\u3010)/.test`, '>', '>=', '<=', '==', '.indexOf', '.startsWith', '.endsWith'],
      expreAll: [{
        value: 0, label: '为空'
      }, {
        value: 1, label: '大于'
      }, {
        value: 2, label: '大于等于'
      }, {
        value: 3, label: '小于等于'
      }, {
        value: 4, label: '等于'
      }, {
        value: 5, label: '包含'
      }, {
        value: 6, label: '以值开始'
      }, {
        value: 7, label: '以值结尾'
      }],
      cities: fields
    }
  },
  methods: {
    checkJS () {
      try {
        // eslint-disable-next-line no-eval
        eval(this.tareaCode)
        this.check_js = 'success'
        this.$message({
          message: '校验成功',
          type: 'success'
        })
      } catch (e) {
        this.check_js = 'warning'
        this.$message({
          message: '校验失败\n' + e.message,
          type: 'warning'
        })
      }
    },
    filterValue (query) {
      if (query !== '') {
        this.cities = fields.filter((item) => {
          // 这里是用的value选项筛选，默认是label
          return item.value.toLowerCase().indexOf(query.toLowerCase()) > -1 || item.label.toLowerCase().indexOf(query.toLowerCase()) > -1
        })
      } else {
        this.cities = fields
      }
    },
    async copy () {
      try {
        await navigator.clipboard.writeText(this.tareaCode)
        this.$message({
          message: '代码复制成功',
          type: 'success'
        })
      } catch (err) {
        console.error('Failed to copy: ', err)
      }
    },
    setCode (e, index) {
      if (e === 'empt') {
        this.selectsCom[index].value = ''
      }
    },
    getcode (v) {
      // eslint-disable-next-line eqeqeq
      if (v == 'empt') {
        this.tareaCode = 'var result = true;\nif(1==1){\n}'
      }
    },
    addGroup () {
      this.selectsCom.push({ groupExpress: '', field: '', express: '', value: '' })
    },
    ctrlInput (e) {
      console.log(e)
    },
    addRowCom () { // 条件新增行
      this.selectsCom.push({ groupExpress: '', field: '', express: '', value: '' })
    },
    delRowCom (index) {
      if (this.selectsCom.length === 1) {
        return
      }
      this.selectsCom.splice(index, 1)
    },
    generateJavaScript () { // 生成js
      this.selectsCom[this.selectsCom.length - 1].groupExpress = ''
      let jscode = 'var result = true;\nif('
      for (const key in this.selectsCom) {
        let val = '\'' + this.selectsCom[key].value + '\' '
        let fed = ''
        fed = ' \'【' + this.selectsCom[key].field + '】\''
        if (this.selectsCom[key].express === 5) {
          val = '(' + val + ')>=0'
        }
        if (this.selectsCom[key].express >= 6) {
          val = '(' + val + ')'
        }
        if (this.selectsCom[key].express === 0) { // express.(‘fed’)
          val = '(' + fed + ')'
          jscode += ' '.concat(this.expare[this.selectsCom[key].express]).concat(val).concat(' ' + this.selectsCom[key].groupExpress)
        } else {
          jscode += (' \'【' + this.selectsCom[key].field + '】\'').concat(this.expare[this.selectsCom[key].express])
            .concat(val).concat(' ' + this.selectsCom[key].groupExpress)
        }
      }
      this.tareaCode = jscode.concat('){\n result = false;\n}')
    }
  }
}
</script>

<style>
.margin-right{
 margin-right: 10px;
}
.div_bjground{
  background-color: #F5F5F6;
  padding:10px;
  max-height: 300px;
  overflow: auto;
}
.el-icon-document-copy:hover {
  color: #5cb6ff;
  cursor: pointer;
}
.item_div {
  margin-bottom: 8px;
}
</style>
